import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter, Link } from 'react-router-dom';
import { technologyActions } from '../../actions';
import { 
    Button, FormGroup
} from 'reactstrap';
import '../../assets/js/jasny-bootstrap';
import { LoadingData } from '../../components/LoadingData';
import { isEqual } from 'lodash';
import { history } from '../../helpers/history';

class TechnologyFormPage extends Component {

    constructor(props) {
        super(props);

        this.state = {
            techId : props.match.params.techId,
            tech : {
                name: '',
                cover_url: null
            },
            imagePreviewUrl: '',
            submitted: false
        };

        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.fileChangedHandler = this.fileChangedHandler.bind(this);

        if(this.state.techId){
            this.title = "Edit Technology";
        }else{
            this.title = "Create Technology";
        }
    }

    componentDidMount() {
        if(this.state.techId){
            this.props.dispatch(technologyActions.getOne(this.props.match.params.techId));
        }
    }

    componentWillReceiveProps(nextProps) {
        const { tech } = this.state;

        if(nextProps.technologies.error){
            history.push('/app/technology');
        }

        if(tech!==nextProps.technologies.item){

            if(nextProps.technologies.item){
                this.setState({
                    tech: {
                        ...tech,
                        name: nextProps.technologies.item.name
                    },
                    imagePreviewUrl: nextProps.technologies.item.cover_url
                });
            }
            
        }
    }

    handleChange(e) {
        const { name, value } = e.target;
        const { tech } = this.state;
        this.setState({
            tech: {
                ...tech,
                [name]: value
            }
        });
    }

    fileChangedHandler = (e) => {
        e.preventDefault();

        let reader = new FileReader();

        const file = e.target.files[0];
        const { name, value } = e.target;
        const { tech } = this.state;
        
        reader.onloadend = () => {
            this.setState({
                tech: {
                    ...tech,
                    [name]: file
                },
                imagePreviewUrl: reader.result
            });
        }

        reader.readAsDataURL(file)
    }

    handleSubmit(e) {
        e.preventDefault();

        this.setState({ submitted: true });
        const { tech } = this.state;
        const { dispatch } = this.props;

        if(this.state.techId){
            if(tech.name){
                dispatch(technologyActions.update(tech, this.state.techId));
            }
        }else{
            if(tech.name && tech.cover_url){
                dispatch(technologyActions.create(tech));
            }
        }
    }

    render(){

        const { tech, imagePreviewUrl, submitted } = this.state;
        const { alert, creating, updating, technologies } = this.props;

        return (
            <div className="container-fluid">
                <div className="row page-titles">
                    <div className="col-md-6 col-8 align-self-center">
                        <h3 className="text-themecolor m-b-0 m-t-0">{this.title}</h3>
                        <ol className="breadcrumb">
                            <li className="breadcrumb-item">
                                <Link to="/app/dashboard">
                                    Home
                                </Link>
                            </li>
                            <li className="breadcrumb-item">
                                <Link to="/app/technology">
                                    Technology
                                </Link>
                            </li>
                            <li className="breadcrumb-item active">{this.title}</li>
                        </ol>
                    </div>
                    <div className="col-md-6 col-4 align-self-center">
                        <Link to="/app/technology" className="btn pull-right hidden-sm-down btn-info btn-sm">
                            <i className="mdi mdi-arrow-left-bold"></i> Back
                        </Link>
                    </div>
                </div>
                <div className="row">
                    <div className="col-12">
                        {alert.message &&
                        <div className={`alert ${alert.type}`}>
                            <button type="button" class="close" data-dismiss="alert" aria-label="Close"> <span aria-hidden="true">&times;</span> </button>
                            {/* <h3 class="text-success"><i class="fa fa-check-circle"></i> Success</h3>  */}
                            {alert.message}
                        </div>
                        }
                        <div className="card card-body">
                            <h4 className="card-title">{this.title}</h4>

                            <form className="form-horizontal m-t-10" onSubmit={this.handleSubmit}>
                                
                                <FormGroup className={(submitted && !tech.name ? ' has-warning' : '')}>
                                    <label className="form-control-label">Technology Name : </label>
                                    <div className="col-xform-control-feedbas-12">
                                        <input className={'form-control '+(submitted && !tech.name ? ' form-control-warning' : '')} name="name" type="text" value={tech.name} onChange={this.handleChange} required="" placeholder="Technology Name" />
                                    </div>
                                    {submitted && !tech.name &&
                                        <small className="form-control-feedback">Name is required</small>
                                    }
                                </FormGroup>

                                <FormGroup className={(this.state.techId) ? '' : (submitted && !tech.cover_url ? ' has-warning' : '')}>
                                    <label className="form-control-label">Cover Photo : </label>
                                    <div className="col-xform-control-feedbas-12">
                                        <input accept="image/*" className={'form-control '+(submitted && !tech.cover_url ? ' form-control-warning' : '')} name="cover_url" type="file" onChange={this.fileChangedHandler} required={(this.state.techId)? false : true} />
                                    </div>
                                    {(this.state.techId) ? '' : submitted && !tech.cover_url &&
                                        <small className="form-control-feedback">Cover photo is required</small>
                                    }
                                    <img src={imagePreviewUrl} height="240px" />
                                </FormGroup>
                                <Button color="success" disabled={technologies.loading || creating} className="waves-effect waves-light m-r-10 btn-sm">
                                    Save 
                                </Button>
                                <Link to="/app/technology" className="btn btn-inverse waves-effect waves-light btn-sm">
                                    Cancel
                                </Link>
                            </form>

                            {creating &&
                                <LoadingData text="Creating..." />
                            }

                            {updating &&
                                <LoadingData text="Updating..." />
                            }

                            {technologies.loading &&
                                <LoadingData text="Getting data..." />
                            }
                        </div>
                    </div>
                </div>
            </div>
        );

    }
}

function mapStateToProps(state) {
    const { authentication, alert, technologies } = state;
    const { creating, updating } = state.technologies;

    return {
        authentication,
        alert,
        creating,
        updating,
        technologies
    }
}

export default withRouter(connect(mapStateToProps)(TechnologyFormPage))


